<template>
	<div class="container">
		<h3 class="bolder">
			<md-icon
				name="authentication"
				size="lg"
				></md-icon>
			{{$system()}}
		</h3>
		<h4>{{$title()}}</h4>
		<div class="bg flex align_center">
			<h1 class="bolder center">{{$school()}}</h1>
		</div>
		<div class="form">
			<md-field>
				<md-input-item
					v-model="account"
					title="账号"
					placeholder="请输入账号/手机号"
					is-title-latent
					clearable
				></md-input-item>
				<md-input-item
					type="password"
					v-model="password"
					title="密码"
					placeholder="请输入密码"
					is-title-latent
					clearable
				></md-input-item>
			</md-field>
			<md-field>
				<md-button type="primary" size="small" round @click="submit">登录</md-button>
			</md-field>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'login',
		data() {
			return {
				account: 'admin',
				password: '!!123abc'
			}
		},
		methods: {
			submit() {
				if (!this.account) {
					this.$toast.info('请输入账号')
					return;
				}
				if (!this.password) {
					this.$toast.info('请输入密码')
					return;
				}
				this.$postL('/users/login', {
					usernbr: this.account,
					password: this.password
				}, (res)=>{
					localStorage.setItem('user', JSON.stringify(res.data))
					if (JSON.parse(localStorage.getItem('user')).open_id) {
						this.$toast.succeed('登录成功')
						this.$router.push('/home')
					} else {
						if (this.is_weixin()) {
							window.location.href = `http://api.jiesitekeji.com/systemApi/wechat/authorize?returnUrl=http://logistic.jiesitekeji.com/dorm_h5/index.html#/home`
						} else {
							this.$toast.succeed('登录成功')
							this.$router.push('/home')
						}
					}
				})
			},
			//鉴别是否是微信
			is_weixin() {
				var ua = window.navigator.userAgent.toLowerCase();
				if (ua.match(/MicroMessenger/i) == 'micromessenger') {
					return true
				} else {
					return false
				}
			}
		}
	}
</script>

<style scoped="scoped">
	h3 {
		font-size: 5vw;
		padding: 4vw;
		color: #666f83;
	}
	h4 {
		padding: 0 4vw;
		color: #666f83;
		font-size: 3vw;
	}
	.bg {
		font-size: 5vw;
		height: 30vw;
		color: #000;
		background: url('../assets/bg.jpg');
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		opacity: 0.5;
		justify-content: center;
		margin-top: 4vw;
	}
	h1 {
		color: #fff;
		font-size: 10vw;
		font-family: cursive;
	}
</style>
